import React from 'react'
import Title from '../../Components/Title/Title'
// import Hero from '../../Components/Hero/Hero'
import Button from '../../Components/Button/Button'

const CareersHero = () => {
  return (
    <div className=''>
      <div className='grid md:grid-cols-2 h-screen pt-12 md:pt-28'>
        <div>
          <Title title="Kickstart your Career with us" fontSize="text-4xl md:text-9xl" />
        </div>
        <div className='flex flex-col gap-8 text-[20px] font-[400] '>
          <p className='leading-[22px]	' data-aos="fade-up">We are constantly on the lookout for skilled and enthusiastic individuals who have the ability to influence both thoughts and tangible outcomes with their innovative ideas. When you become a member of our team, you are committing to a long-term journey. We are always here to listen to your concepts, no matter how crazy they are. So, who knows  you might be the one reshaping the world out there!</p>
          <p className='leading-[22px]	' data-aos="fade-up">If you are an aspiring, creative go-getter with a bunch of ideas and relevant industry exposure, feel free to reach out to us.</p>
          <p className='leading-[22px]	' data-aos="fade-up">email your resume at</p>
          <Button name="creative@maid.com" fontSize="text-sm"/>
        </div>
      </div>
    </div>
  )
}

export default CareersHero