import React from "react";
import Button from "../../Components/Button/Button";
import Title from "../../Components/Title/Title";

const Section = (props) => {
  return (
    <div >
      <div className={`block grid md:grid-cols-2 gap-8 md:gap-12`}>
      <div>
        <Title title={props.title} />
      </div>
        <div className="grid gap-8">
        <div className="grid gap-4">
        <p className="text-xl font-bold" data-aos="fade-up">Job Summary</p>
        <p className="text-[#ccc]" data-aos="fade-up">{props.description}</p>
        </div>
        <div className="grid gap-4">
            <p className="text-xl font-bold" data-aos="fade-up">Responsibilities and Duties</p>
            <div className='grid' data-aos="fade-up">
                {props.List.map((item)=>(
                    <li className='text-lg text-[#ccc] hover:text-[#f6891f] duration-300 cursor-pointer'>
                        {item}
                    </li>
                ))}
        </div>
        </div>
          <Button name="creative@maid.com" fontSize="text-sm" />
        </div>
      </div>
    </div>
  );
};

export default Section;
