import React, { useEffect, useRef } from "react";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import gsap from "gsap";

gsap.registerPlugin(ScrollTrigger);


const list = [
  {
    title: "Our Crew",
    description:
      "We believe that the best works are created by happy minds. Therefore, in an effort to create equal opportunities and a happy environment, we foster a sense of belonging and visibility among our team members. We provide fair and timely compensation that makes it easier for them to live well and support their loved ones too.",
  },
  {
    title: "Our Clients",
    description:
      "Over the years, we have cultivated strong connections with those who have been a part of our journey. We believe they are no longer our clients, but our teammates who join us in our collaborative efforts. We recognise them as our pillars of support to aid us in fulfilling our shared vision and goals.",
  },
  {
    title: "Our Communities",
    description:
      "One of our shared goals is to enhance the overall quality of life. We identify communities that seek support and through our collaborations with NGOs and charitable entities, we aspire to positively impact more lives.",
  },
];

const CareSection = () => {

  
  const elementRef = useRef(null);

  useEffect(() => {
    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: elementRef.current,
        pin: true,
        start:"top center",
        end:"+=1600",
        scrub: true,
        // markers: true,
      },
    });

    return () => {
      tl.kill();
    };
  }, [elementRef]);

  return (
    <div className="relative">
      <div className="px-8 py-8 md:py-16 grid gap-8 ">
        <div className="hidden md:grid  md:grid-cols-2 gap-8">
          <div className=" grid
           place-items-center 
           h-screen
          ">
            <p ref={elementRef} className=" trigger grid place-items-center  text-4xl md:text-7xl font-bold  md:leading-tight bg-clip-text bg-gradient-to-b from-[#f6891f] via-[#f6891f] to-rose-600 text-transparent">
              We Care
            </p>
          </div>
          <div className="grid gap-20 flex-none min-w-full px-4 sm:px-6 md:px-0">
            {list.map((item) => (
              <div className="h-screen flex flex-col justify-center gap-8">
                <p className="border-[#fff] font-bold text-3xl md:text-7xl">
                  {item.title}
                </p>
                <p className="border-[#fff] text-sm lg:text-auto">
                  {item.description}
                </p>
              </div>
            ))}
          </div>
        </div>
        <div className="md:hidden grid md:grid-cols-2 gap-8">
          <div className=" grid
         
           
          ">
            <p className=" trigger grid  text-4xl md:text-7xl font-bold  md:leading-tight bg-clip-text bg-gradient-to-b from-[#f6891f] via-[#f6891f] to-rose-600 text-transparent" data-aos="fade-up" data-aos-duration="2000">
              We Care
            </p>
          </div>
          <div className="grid gap-20 flex-none min-w-full  md:px-0">
            {list.map((item) => (
              <div className=" flex flex-col justify-center gap-8">
                <p className="border-[#fff] font-bold text-3xl md:text-7xl" data-aos="fade-up" data-aos-duration="2000">
                  {item.title}
                </p>
                <p className="border-[#fff] text-sm lg:text-auto" data-aos="fade-up" data-aos-duration="2000">
                  {item.description}
                </p>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default CareSection;
