import React from "react";
import ClientItem from "../../Components/Clients/ClientItem";
import Title from "../../Components/Title/Title";
import { clients_logo } from "../../Components/Clients/clients_logo";

const Clients = (props) => {
  return (
    <div className="md:px-44 grid gap-8 py-16 ">
      <div className="px-8 md:px-0 ">
        <Title
          title="Our Clients"
        />
      </div>

      <div className="grid grid-cols-3 md:grid-cols-4 lg:grid-cols-6 md:gap-4">
      {clients_logo.map(item=>(
        <ClientItem Clients={item.img} />
      ))}
      </div>
    </div>
  );
};

export default Clients;
