import React from 'react'
// import Button from '../Button/Button'

const NewsItem = (props) => {
  return (
    <div className='' >
        <a href={props.url} className='text-[#fff] grid gap-6 group'>
            <div className='w-full  overflow-hidden md:min-h-[320px]'>
                <img src={props.img} alt="news" className='group-hover:scale-105 duration-500' />
            </div>
            <span className='text-[#ccc] text-xl'>{props.date}</span>
            <p className='font-bold text-xl md:text-3xl'>{props.title}</p>
            <p>{props.description}</p>
        </a>
    </div>
  )
}

export default NewsItem