import React, { useState } from "react";
import { Box, Typography, IconButton } from "@mui/material";
import { HomeIcon, ArrowDownTrayIcon } from "@heroicons/react/24/solid";

function ExpandableBox(props) {
  const [expanded, setExpanded] = useState(props.state);

  const handleExpand = () => {
    setExpanded(!expanded);
  };

  return (
    <Box
      className={`flex flex-col ${
        expanded ? "h-full" : "h-16"
      } transition-all duration-300`}
      sx={{
        backgroundColor: "#fff1",
        borderRadius: 4,
        padding: 2,
        // margin: 2,
        border: "1px solid #fff",
      }}
    >
      <Box onClick={handleExpand} className="flex justify-between items-center">
        <Typography variant="h6" className="text-lg font-bold">
          What Digital Marketing Services Do You Offer?
        </Typography>
        <IconButton
          sx={{ color: "orange" }}
          onClick={handleExpand}
          className="text-gray-500"
        >
          {expanded ? (
            <ArrowDownTrayIcon size="2" />
          ) : (
            <ArrowDownTrayIcon size="2" />
          )}
        </IconButton>
      </Box>
      {expanded && (
        <Box className="pt-4">
          <Typography variant="body2">
            our Digital Marketing Agency offers a comprehensive suite of
            services, including SEO, PPC, and SMM. We specialize in crafting
            tailored strategies that enhance online visibility, drive targeted
            traffic, and elevate brands in Saudi Arabia. Trust us to transform
            your digital presence and boost your business.
          </Typography>
        </Box>
      )}
    </Box>
  );
}

export default ExpandableBox;
