import React from "react";
import Card from "../../Components/Card/Card";
import card_img from "../../Assets/card_creative-01.png";
import ServicesDetails from "../Services/servicesDetails";
const services = [
  {
    title: "Immersive Brands",
    description:
      "We elevate your brands essence by crafting a visual and verbal symphony that resonates, making your brand memorable.",
    btn: "BRANDING SERVICES",
    url: "../Services/servicesDetails",
  },
  {
    title: "Interactive Digital",
    description:
      "Experience digital innovation with interactive website design and app development. We craft immersive online experiences that blend captivating design with seamless functionality",
    btn: "DIGITAL SERVICES",
    url: "../Services/servicesDetails",
  },
  {
    title: "Effective Communication",
    description:
      "Discover unparalleled growth with our communication strategies, clever marketing campaigns, and ads that elevate engagement, to create lasting impressions.",
    btn: "MARKETING SERVICES",
    url: "../Services/servicesDetails",
  },
];

const Services = () => {
  return (
    <div
      className="relative"
      style={{ backgroundImage: `url(${"src/Assets/card_creative-01.png"})` }}
    >
      <div className="absolute bottom-[50%]  right-[50%] md:bottom-auto md:right-auto z-[0] grid md:-translate-y-[180px] scale-[300%] md:scale-100">
        <img src={card_img} alt="" />
      </div>
      <div className="relative z-[1] grid md:grid-cols-3 md:gap-y-16 p-8 md:py-16 md:divide-x divide-orange-700/50">
        {services.map((item) => (
          <Card
            title={item.title}
            description={item.description}
            btn={item.btn}
            url={item.url}
          />
        ))}
        {/* <Card />
        <Card />
        <Card /> */}
        {/* <Card />
        <Card /> */}
      </div>
    </div>
  );
};

export default Services;
