import React from "react";
import Button from "../../../Components/Button/Button";

const MainSection = (props) => {
  return (
    <div className="grid md:grid-cols-2 gap-8">
      <div className="grid gap-8">
        <h1 className="md:leading-tight font-bold text-3xl md:text-6xl md:font-bold   bg-clip-text bg-gradient-to-r from-[#f6891f] to-rose-600 text-transparent">
          {props.title}
        </h1>
        <p className="text-sm">{props.subText}</p>
        <Button name="Contact Us" />
      </div>
      <div>
        <img src={props.img} alt="logo3d" />
      </div>
    </div>
  );
};

export default MainSection;
