import React from "react";
import Title from "../../../Components/Title/Title";
import logo_sign from "../../../Assets/logo-sign-02.svg";
import Button from "../../../Components/Button/Button";

const Sections = (props) => {
  return (
    <div className="py-8 grid gap-8">
      <Title
        title={props.title}
        fontSize="text-6xl md:text-[140px]"
      />
      <p className="md:text-xl">{props.subText}</p>
      <div className="py-8 grid md:grid-cols-3 gap-8">
        {props.listData.map((item) => (
          <div className="">
            <div className="after:bg-gradient-to-r after:from-red-500 after:to-orange-500 after:h-[2px] after:w-full after:block ">
              <div className={`flex gap-4 ${props.subText === "" ? "md:min-h-[340px]": "md:min-h-[160px]"}`}>
                <img className="size-7 mt-1" src={logo_sign} alt="logo" />
                <div className="flex flex-col gap-2 md:gap-8 pb-8">
                  <p className="text-2xl md:text-5xl font-bold ">{item.title}</p>
                  <p className="text-sm md:text-xl md:leading-tight">{item.text}</p>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
      <Button name={props.btn} />
    </div>
  );
};

export default Sections;
