import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import React, { useEffect, useState } from "react";
import Title from "../../Components/Title/Title";
import NewsItem from "../../Components/News/NewsItem";
import article_1 from "../../Assets/blog/Web-designing-saudi-1 (1).webp";
import article_2 from "../../Assets/blog/Resp-Web-Design-Saudi-1.webp";

const MediaCenter = () => {
  const [swiper, setSwiper] = useState(null);

  useEffect(() => {
    const intervalId = setInterval(() => {
      if (swiper) {
        swiper.slideNext();
      }
    }, 5000);

    return () => {
      clearInterval(intervalId);
    };
  }, [swiper]);

  return (
    <div className="px-8 md:px-44 py-16 creative-bg2">
      <Title title="Media Center" />

      <Swiper
        spaceBetween={50}
        slidesPerView={1}
        breakpoints={{
        640: { // breakpoint for mobile devices (width < 640px)
          slidesPerView: 2,
        },
      }}
        loop={true}
        onSwiper={(swiperInstance) => {
          setSwiper(swiperInstance);
        }}
      >
        <SwiperSlide>
          <NewsItem
            title="The Evolution of Responsive Web Design: Adapting to New Devices and Screen Sizes"
            date="Jun 28, 2024"
            img={article_2}
            url=""
          />
        </SwiperSlide>

        <SwiperSlide>
          <NewsItem
            title="Leading Website Designing Companies in Saudi Arabia"
            date="Jun 27, 2024"
            img={article_1}
            url=""
          />
        </SwiperSlide>
        <SwiperSlide>
          <NewsItem
            title="The Evolution of Responsive Web Design: Adapting to New Devices and Screen Sizes"
            date="Jun 28, 2024"
            img={article_2}
            url=""
          />
        </SwiperSlide>
        <SwiperSlide>
          <NewsItem
            title="Leading Website Designing Companies in Saudi Arabia"
            date="Jun 27, 2024"
            img={article_1}
            url=""
          />
        </SwiperSlide>
      </Swiper>
    </div>
  );
};

export default MediaCenter;
