import React from 'react'
import { Link } from 'react-router-dom'

const services_List = [
    {
        name: "Branding Services",
        url: "",
    },
    {
        name: "Digital Services",
        url: "",
    },
    {
        name: "Marketing Services",
        url: "",
    },
]


const ServicesList = () => {
  return (
    <div className='sticky top-0 bg-[#2229]'>
        <div className='md:flex gap-4 pt-2 justify-center items-center grid text-center'>
            {services_List.map(item=>(
                <div className='px-2  md:py-6 text-xl'>
                    <Link to={item.url}>{item.name}</Link>
                </div>
            ))}
        </div>
    </div>
  )
}

export default ServicesList