import React from "react";

const AboutSection = () => {
  return (
    <div className={` creative-bg `}>
      <div className="px-8 md:px-8 py-8 md:py-16 bg-gradient-to-t from-[#111] to-[30%] to-[#0000]">
        <div className="grid">
          <div className="grid gap-2 md:gap-2 " data-aos="fade-up">
            <p>We are</p>
            <p className="text-3xl md:text-5xl font-bold   bg-clip-text bg-gradient-to-r from-[#f6891f] via-orange-300 to-rose-600 text-transparent">
            KSAs leading AI-first digital transformation company. In a world of monotonous templates and words, we spark creativity and meticulously craft experiences that evoke a sense of awe.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AboutSection;
