import React from 'react'

const ClientItem = (props) => {
  return (
    <div className=' grid place-items-center min-h-[180px] h-full w-full border border-[#fff6] ' data-aos="zoom-in">
    <div className=' text-[#fff] min-h-[180px]'>
    <img src={props.Clients} alt="clients"/>
    </div>
    </div>
  )
}

export default ClientItem