import React, { useEffect, useState } from "react";
import { projects } from "../../../config/projectsList";

const ProjectItem = (props) => {
  const [filteredProjects, setFilteredProjects] = useState(projects);

  useEffect(()=>{
    const filteredProjects = projects.filter((project) => {
      return project.category.toLowerCase() === props.category;
    });
    setFilteredProjects(filteredProjects);
  }, [props.category])

  return (
    <div className="flex flex-col w-full ">
      <div className="p-1 grid sm:grid-cols-3 gap-1">
        {filteredProjects.map((item) => (
          <div className="group relative overflow-hidden max-h-[320px]">
            <a href="/portfolio" className="relative overflow-hidden h-full flex justify-center items-center">
              <img height="100%"
                className="group-hover:scale-105 duration-300 w-full"
                src={item.img}
                alt={item.title}
              />
            </a>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ProjectItem;
