import React, { useEffect, useRef, useState } from "react";
import logo3D from "../../Assets/logo-3d.png";
import Button from "../../Components/Button/Button";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import gsap from "gsap";
import { useGSAP } from "@gsap/react";

gsap.registerPlugin(useGSAP, ScrollTrigger);


const AboutSection = () => {

  const main = useRef();

  useGSAP(
    () => {
      
    },
    { scope: main }
  );

  const [height] = useState(window.innerHeight);
  const elementRef = useRef(null);
  const elements = useRef([]);




  useEffect(() => {

    gsap.utils.toArray(elements.current).forEach((element, index) => {
      gsap.to(element, {
        // color:'#0000',
        scrollTrigger: {
          trigger: element,
          toggleClass: `color-dark`,
          start:"top top",
          end:`+=${height}`,
        },
        // duration: 9,

      });
    });
    
    

    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: elementRef.current,
        pin: true,
        start:"-5% top",
        end:`+=${height}`,
        scrub: true,
        // markers: true,
      },
    });

    return () => {
      tl.kill();
    };
  }, [height]);

  return (
    <div  id="id" className={`h-screen-2 class creative-bg `}>
      <div ref={elementRef} className="trigger h-screen container  px-8 md:px-44 py-8 md:py-16 bg-gradient-to-t from-[#111] to-[30%] to-[#0000] grid place-items-center ">
        <div className="grid md:grid-cols-12">
          <div className="grid gap-2 md:gap-4 col-span-7">
            <p className="text-4xl md:text-6xl md:font-bold  md:leading-tight bg-clip-text bg-gradient-to-r from-[#f6891f] to-rose-600 text-transparent ">
            Digital transformation agency in Saudi Arabia
              <div className="box flex flex-wrap gap-x-4 text-[#fff]" ref={main}>
                <p ref={(el) => (elements.current[0] = el)} className="" >crafting</p>
                <p ref={(el) => (elements.current[2] = el)} className="" >experiences</p>
                <p ref={(el) => (elements.current[4] = el)} className="" >with</p>
                <p ref={(el) => (elements.current[6] = el)} className="" >passion,</p>
                <p ref={(el) => (elements.current[8] = el)} className="" >purpose,</p>
                <p ref={(el) => (elements.current[10] = el)} className="" >and</p>
                <p ref={(el) => (elements.current[12] = el)} className="" >a vision</p>
                <p ref={(el) => (elements.current[14] = el)} className="" >for</p>
                <p ref={(el) => (elements.current[16] = el)} className="" >success</p>
                <p ref={(el) => (elements.current[18] = el)} className="" >.</p>
              </div>
            </p>
            <p className="text-sm font-thin">
            Let your imagination meet our creative solutions and pave your way to success


            </p>
            <div className="">
              <Button name="more" />
            </div>
          </div>
          <div className="col-span-5 grid place-items-center py-4 px-16 md:px-0">
            <img className="w-full" src={logo3D} alt="logo3d"/>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AboutSection;
