import { yupResolver } from "@hookform/resolvers/yup";
import { Box, Grid, TextField } from "@mui/material";
import React from "react";
import { useForm } from "react-hook-form";
import { schema } from "./useValidLogin";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import { orange, yellow } from "@mui/material/colors";
import Button from "../../../Components/Button/Button";

const darkTheme = createTheme({
  palette: {
    primary: orange,
    secondary: yellow,
    mode: "dark",
  },
});

const Form = () => {
  const {
    register,
    formState: { errors },
    handleSubmit,
  } = useForm({
    resolver: yupResolver(schema),
  });

  const onSubmit = async (data) => {
    try {
      //   const response = await instanceAxios.post("login", data);
      //   console.log(response.data);
      //   if (data.token) {
      //     localStorage.setItem("token", data.token);
      //   }
    } catch (error) {
      //   console.error(error);
    }
  };

  return (
    <div className="">
      <div className="md:py-12 px-4">
        <ThemeProvider theme={darkTheme}>
          <CssBaseline />

          <form onSubmit={handleSubmit(onSubmit)} className="">
            <Grid container gap={2} flexDirection={"column"}>
              <div className="grid md:grid-cols-2 gap-8">
              <TextField
                type="text"
                error={errors.name?.message ? true : false}
                helperText={errors.name?.message}
                {...register("name")}
                placeholder="Tell us your name"
                label="Tell us your name"
                id="name"
                fullWidth
              />
              <TextField
                type="email"
                error={errors.email?.message ? true : false}
                helperText={errors.email?.message}
                {...register("email")}
                placeholder="Your Email"
                label="Your Email"
                id="email"
                fullWidth
              />
              <TextField
                type="number"
                error={errors.name?.message ? true : false}
                helperText={errors.name?.message}
                {...register("phone")}
                placeholder="Your phone"
                label="Your phone"
                id="phone"
                fullWidth
              />
              <TextField
                type="text"
                error={errors.company?.message ? true : false}
                helperText={errors.company?.message}
                {...register("company")}
                placeholder="Your company"
                label="Your company"
                id="company"
                fullWidth
              />
              </div>
              <TextField
                type="textarea"
                error={errors.text?.message ? true : false}
                helperText={errors.text?.message}
                {...register("text")}
                placeholder="What is your requirement?"
                label="What is your requirement?"
                id="message"
                fullWidth
                multiline
                minRows={7}
              />
              <Box>
                <Button name="Send inquiry" type="submit" variant="contained" />
              </Box>
            </Grid>
          </form>
        </ThemeProvider>
      </div>
    </div>
  );
};

export default Form;
